.main-container {
  background-color: #1b2330;
  min-height: 100vh;
  width: 100vw;
}
@media only screen and (max-width: 600px) {
  #root .main-content p {
    position: relative;
    z-index: 50;
    /* background-color: #fff; */
  }
  .main-container {
    width: 100%;
  }

  #root .btn-get-started {
    width: 280px;
    position: relative;
    z-index: 500;
  }
  #root h2 {
    width: 320px;
    height: 104px;
    color: #ffffff;
    font-family: Raleway;
    font-size: 24px;
    font-weight: 700;
    line-height: 36px;
  }

  #root .main-content p {
    width: 296px;
    height: 83px;
    color: #ffffff;
    font-family: "Open Sans";
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
  }
  #root .main-pic {
    width: 304px;
    height: 225px;
  }
  .opts-grid {
    margin-top: 30vh !important;
    grid-template-columns: 1fr !important;
  }
  .logo {
    width: 80px !important;
  }
  .bgc img {
    top: 120vh !important;
  }
  #root .logoPic {
    width: 28px;
    height: 23px;
  }

  #root .logoText {
    width: 46px;
    height: 24px;
  }
  #root header {
    right: 20px;
    top: 60px;
  }
  #root header a {
    font-size: 16px;
  }
}
.logo {
  position: absolute;
  top: 60px;
  left: 5vw;
  width: 180px;
}

.logoPic {
  width: 60px;
  height: 49px;
}

.logoText {
  width: 100px;
  height: 52px;
}

header {
  position: absolute;
  right: 80px;
  top: 70px;
}

header a {
  /* Style for "Sign In" */
  width: 50px;
  height: 19px;
  color: #ffffff;
  font-family: Raleway;
  font-size: 19px;
  margin-left: 56px;
}

.main-content {
  padding-top: 15vh;
  position: relative;
  z-index: 2;
  width: 100vw;
}

.main-content > * {
  position: relative;
  z-index: 2;
}

@media only screen and (max-width: 600px) {
  .content {
    width: 100vw;
  }
}

.main-content p {
  width: 589px;
  height: 88px;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
}
.main-pic {
  width: 25vw;
  height: auto;
  /* position: absolute;
  top: 25vh;
  left: 37.5vw; */
}

h2 {
  /* Style for "All your f" */
  width: 60vw;
  color: #ffffff;
  font-family: Raleway;
  font-size: 40px;
  font-weight: 700;
  line-height: 60px;
}

.btn-get-started {
  width: 280px;
  height: 56px;
  color: white;
  box-shadow: 2px 3px 6px rgba(0, 0, 0, 0.16);
  border-radius: 28px;
  background-image: linear-gradient(152deg, #63e1d9 0%, #34a0cd 100%);
}

.secondary-bg {
  background-color: #181f2b;
  width: 100vw;
  margin-top: -1vh;
  padding-bottom: 30vh;
  position: relative;
}
.opts-grid {
  width: 80vw;
  margin-top: -15vh;
  margin-bottom: 10vh;
  display: grid;
  grid-gap: 5vw;
  grid-template-columns: 1fr 1fr;
}

.opts-grid div {
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 250px;
  position: relative;
  z-index: 1;
}
.opts-grid h3 {
  /* width: 228px;
  height: 23px; */
  color: #ffffff;
  font-family: Raleway;
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 8px;
}
.opts-grid img {
  /* width: 75px; */
  height: 80px;
  margin-bottom: 25px;
}

.opts-grid p {
  /* Style for "Securely s" */
  width: 363px;
  height: 62px;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 400;
  line-height: 21px;
}

