.productive-container {
  width: 80vw;
}

.productive-container h4 {
  width: 506px;
  height: 99px;
  color: #ffffff;
  font-family: Raleway;
  font-size: 40px;
  font-weight: 700;
  line-height: 50px;
}

.productive-container p {
  width: 563px;
  height: 46px;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.productive-container span {
  /* Style for "See how Fy" */
  width: 146px;
  height: 22px;
  color: #62e0d9;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 400;
  border-bottom: 1px solid #62e0d9;
}

.productive-container span img {
  width: 12px;
  height: 12px;
  box-shadow: 0 0 2px rgba(98, 224, 217, 0.81);
}

.bgc-container {
  margin-top: -35vh;
  position: relative;
  z-index: 0;
}
.bgc {
  width: 100vw;
  top: 90vh;
  left: 0px;
}

/* // review */
@media only screen and (max-width: 600px) {
  #root .review-grid {
    grid-template-columns: 1fr;
    left: 0vw;
  }
}
.review-grid {
  margin-top: 15vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: 70vw;
  left: -5vw;
  position: relative;
  z-index: 2;
  grid-gap: 20px;
}

.review-card {
  width: 360px;
  height: 200px;
  box-shadow: 5px 5px 8px 4px rgba(56, 56, 56, 0.1);
  border-radius: 4px;
  background-color: #202a3c;
  padding: 30px 20px;
}

.review-card h4 {
  width: 65px;
  height: 12px;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 0.5px;
  line-height: 12px;
  margin: 0 auto;
}

.review-card p {
  width: 310px;
  height: 80px;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.5px;
  line-height: 21px;
  padding: 0px;
  /* margin-left: 0px; */
  margin-bottom: 50px;
  text-align: start;
}

.review-card h5 {
  width: 86px;
  /* height: 12px; */
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 0.5px;
  margin: 0px;
  margin-bottom: 10px;
  /* line-height: 12px; */
}
.review-card span {
  width: 88px;
  height: 10px;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 7px;
  font-weight: 400;
  letter-spacing: 0.5px;
  line-height: 10px;
}

.review-card-name {
  width: 200px;
  text-align: start;
  height: 50px;
  margin-top: 20px;
  padding: 15px;
}
.review-card img {
  width: 24px;
  height: 24px;
  border-radius: 50%;
}

.quotes {
  position: absolute;
  top: -4vh;
  left: -1vw;
  z-index: -1;
}

/* // bottom modal  */

@media only screen and (max-width: 600px) {
  #root .bottom-modal {
    padding-bottom: 15vh;

    bottom: 150vh;
  }

  #root .bottom-modal p {
    font-size: 16px;
  }

  #root .input-btn-container {
    flex-direction: column;
  }
  #root .input-btn-container input {
    width: 280px;
    height: 48px;
    margin-bottom: 15px;
  }

  #root .btn-get-started-free {
    width: 280px;
    height: 48px;
    padding: 5px 20px;
  }
}
.bottom-modal {
  width: 70vw;
  height: 275px;
  box-shadow: 5px 5px 8px rgba(0, 0, 0, 0.25);
  border-radius: 9px;
  background-color: #1b2330;
  margin-top: -15vh;
  position: absolute;
  bottom: 60vh;
  left: 15vw;
  z-index: 3;
  padding-bottom: 5vh;
}

.bottom-modal h5 {
  /* Style for "Get early" */
  width: 347px;
  /* height: 48px; */
  color: #ffffff;
  font-family: Raleway;
  font-size: 32px;
  font-weight: 700;
  line-height: 48px;
  margin-bottom: 20px;
}

.bottom-modal p {
  width: 673px;
  height: 41px;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  font-size: 17px;
}

.input-btn-container {
  width: 50vw;
}

.input-btn-container input {
  width: 480px;
  height: 48px;
  box-shadow: 2px 3px 6px rgba(7, 4, 59, 0.16);
  border-radius: 24px;
  background-color: #ffffff;
  padding: 5px 20px;
  border: none;
}
.btn-get-started-free {
  width: 200px;
  height: 48px;
  box-shadow: 2px 3px 6px rgba(7, 4, 59, 0.16);
  border-radius: 24px;
  background-image: linear-gradient(152deg, #63e1d9 0%, #34a0cd 100%);
  color: white;
}

